import { getSmartAccountAddress, tronAddressFromHex, type ERC4337Options } from '@particle-network/auth-core';
import { chains } from '@particle-network/chains';
import { isTron } from '../utils/chain-utils';

type Options = {
    chainId?: number;
    erc4337?: ERC4337Options;
};

export const getEVMPublicAddress = async ({ chainId, erc4337 }: Options): Promise<string> => {
    const id = chainId || Number(window.particleAuth?.ethereum.chainId);
    let address;
    if (erc4337) {
        address = await getSmartAccountAddress(erc4337);
    } else {
        if (typeof window !== 'undefined') {
            address = window.particleAuth?.ethereum.selectedAddress;
        }
    }

    if (isTron(chains.getEVMChainInfoById(id))) {
        address = tronAddressFromHex(address);
    }
    return address || '';
};
