import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import resources from './locales/resources';

export const i18nInit = (lng = 'en') => {
    i18n.use(initReactI18next).init({
        fallbackLng: 'en',
        resources,
        lng: lng ? lng.replace('_', '-') : undefined,
        interpolation: {
            escapeValue: false,
        },
    });
};
export default i18n;
