import { isConnected } from '@particle-network/auth-core';
import qs from 'qs';
import React, { useMemo } from 'react';
import type { ICustomRouter } from '../../context/types';
import ParticleLoading from '../loading';

export enum AuthPage {
    Index = 'index',
    Login = 'login',
    EvmSign = 'evm-chain/sign',
    SolanaSign = 'solana/sign',
    LoginAccount = 'login-account',
    AccountSecurity = 'account/security',
    MasterPasswordVerify = 'account/master-password/verify',
    MasterPasswordPrompt = 'account/master-password/prompt',
}

interface IPage {
    name?: AuthPage;
    path?: string;
    component: React.LazyExoticComponent<any>;
}

const pages: IPage[] = [
    {
        name: AuthPage.Index,
        component: React.lazy(() => import('../../pages/index/index')),
    },
    {
        name: AuthPage.Login,
        component: React.lazy(() => import('../../pages/login')),
    },
    {
        path: 'account/security',
        component: React.lazy(() => import('../../pages/account/security')),
    },
    {
        name: AuthPage.LoginAccount,
        path: 'login-account',
        component: React.lazy(() => import('../../pages/loginAccount/index')),
    },
    {
        path: 'manageDevices/deviceList',
        component: React.lazy(() => import('../../pages/manageDevices/deviceList/index')),
    },
    {
        path: 'manageDevices/deviceDetails',
        component: React.lazy(() => import('../../pages/manageDevices/deviceDetails/index')),
    },
    {
        name: AuthPage.EvmSign,
        component: React.lazy(() => import('../../pages/sign/index')),
    },
    {
        name: AuthPage.SolanaSign,
        component: React.lazy(() => import('../../pages/sign/components/info-sign')),
    },
    {
        path: 'account/bind',
        component: React.lazy(() => import('../../pages/account/accountBind/index')),
    },
    {
        path: 'account/verify',
        component: React.lazy(() => import('../../pages/account/accountVerify/index')),
    },
    {
        path: 'login-account',
        component: React.lazy(() => import('../../pages/loginAccount/index')),
    },
    {
        path: 'manageDevices/deviceDetails',
        component: React.lazy(() => import('../../pages/manageDevices/deviceDetails/index')),
    },
    {
        path: 'account/master-password',
        component: React.lazy(() => import('../../pages/account/setMasterPassword/index')),
    },
    {
        path: 'account/master-password/description',
        component: React.lazy(() => import('../../pages/account/masterPasswordDescription/index')),
    },
    {
        path: AuthPage.MasterPasswordVerify,
        component: React.lazy(() => import('../../pages/account/masterPasswordVerify/index')),
    },
    {
        path: AuthPage.MasterPasswordPrompt,
        component: React.lazy(() => import('../../pages/account/masterPasswordModal/index')),
    },
    {
        path: 'account/master-password/change',
        component: React.lazy(() => import('../../pages/account/masterPasswordChange/index')),
    },
    {
        path: 'account/set-password',
        component: React.lazy(() => import('../../pages/account/setPaymentPassword/index')),
    },
    {
        path: 'account/change-password',
        component: React.lazy(() => import('../../pages/account/changePaymentPassword/index')),
    },
    {
        path: 'login-account/bind',
        component: React.lazy(() => import('../../pages/loginAccountBind/index')),
    },
    {
        path: 'login-account/bind-loading',
        component: React.lazy(() => import('../../pages/account/loginAccountBindLoading/index')),
    },
];

const CustomRouter = (props: ICustomRouter) => {
    const path = useMemo(() => {
        let path = props.path?.split('?')?.[0].replace(/^\//, '');
        if (path == '' || path == '/') {
            path = AuthPage.Index;
        }
        if (path !== AuthPage.Login && !isConnected() && path !== AuthPage.Index) {
            path = AuthPage.Login;
        }
        return path;
    }, [props.path]);

    const queryString = useMemo(() => {
        let queryString = props.path?.split('?')?.[1];
        if (path !== AuthPage.Login && !isConnected() && path !== AuthPage.Index) {
            queryString = '';
        }
        return queryString;
    }, [props.path, path]);

    const state = useMemo(() => {
        return {
            ...(props.state || {}),
            ...qs.parse(queryString),
        };
    }, [props.state, queryString]);

    const Comp = useMemo(() => {
        let Comp = pages.find((item: IPage) => item.name === path || item.path === path);
        if (!Comp) {
            if (path) {
                console.log('Not Found', path);
            }
            Comp = pages[0];
        }
        return Comp;
    }, [path]);

    return (
        <div data-path={path} data-state={qs.stringify(state)} key={path}>
            <React.Suspense
                fallback={
                    <div className="lazy-loaing-container">
                        <ParticleLoading />
                    </div>
                }
            >
                <Comp.component {...state} children={props.children} />
            </React.Suspense>
        </div>
    );
};

export default CustomRouter;
