import { isHexString } from '@ethereumjs/util';
import { tronAddressFromHex } from '@particle-network/auth-core';
import { Ethereum, chains, type Chain, type ChainInfo } from '@particle-network/chains';

export const getChainDisplayName = (chain: Chain) => {
    const chainInfo = getChainInfo(chain);
    if (!chainInfo) {
        return '';
    }
    if (chainInfo.network === 'Mainnet') {
        return chainInfo.fullname.replace(' Mainnet', '');
    } else {
        return chainInfo.fullname;
    }
};

export function getNativeSymbol(chain: Chain): string {
    return getChainInfo(chain)?.nativeCurrency?.symbol || '';
}

export function getChainIcon(chain: Chain): string {
    return formatPng(getChainInfo(chain)?.icon ?? defaultTokenIcon);
}

export function getChainInfo(chain: Chain): ChainInfo | undefined {
    return chains.getChainInfo(chain);
}

export function getEvmChain(id: number) {
    return chains.getEVMChainInfoById(id);
}

export function getSolanaChain(id: number) {
    return chains.getSolanaChainInfoById(id);
}

export function formatAddress(address: string, chain: Chain): string {
    if (isTron(chain) && isHexString(address)) {
        return tronAddressFromHex(address);
    }
    return address;
}

export const defaultTokenIcon = 'https://static.particle.network/token-list/defaultToken/default.png';

export function isSolana(chain: Chain) {
    return chain.name.toLowerCase() === 'solana';
}

export function isEvm(chain: Chain) {
    return chain.name.toLowerCase() !== 'solana';
}

export function isTron(chain?: Chain) {
    return chain && chain.name.toLowerCase() === 'tron';
}

export const formatPng = (src = '') => {
    if (!src) return src;
    return src + (src.includes('?') ? '&' : '?') + 'x-oss-process=image/format,png';
};

export function getNativeTokenIcon(chain?: Chain): string {
    if (chain) {
        const chainInfo = getChainInfo(chain);
        if (chainInfo) {
            if (chainInfo.nativeCurrency.symbol === 'ETH') {
                return Ethereum.icon;
            } else {
                return chainInfo?.nativeIcon || chainInfo?.icon || defaultTokenIcon;
            }
        } else {
            return defaultTokenIcon;
        }
    }
    return defaultTokenIcon;
}
