import {
    getLatestAuthType,
    getUserInfo,
    isNullish,
    requireNotNullish,
    type ERC4337Options,
} from '@particle-network/auth-core';
import type { Chain } from '@particle-network/chains';
import base58 from 'bs58';
import { v4 as uuidv4 } from 'uuid';
import { getEVMPublicAddress } from '../api/getPublicAddress';
import type { LoginAuthorization, SolanaAuthorization } from '../types/connectOptions';
import { isSolana } from './chain-utils';
import { shortString, toHexPrefixString } from './common-utils';

export type AuthorizationResult = {
    message: string;
    signature: string;
};

//https://eips.ethereum.org/EIPS/eip-4361
const signInEthereumMessage = `{0} wants you to sign in with your Ethereum account:
{1}

{2}

URI: https://{3}/
Version: 1
Chain ID: {4}
Nonce: {5}
Issued At: {6}
Expiration Time: {7}`;

const authorizeUtils = {
    isNeedAuthorize({ authorization, chain }: LoginAuthorization): boolean {
        if (!authorization) {
            return false;
        }
        if (chain.name.toLowerCase() === 'solana') {
            return (
                'message' in authorization &&
                !isNullish(authorization.message) &&
                'uniq' in authorization &&
                !isNullish(authorization.uniq)
            );
        } else {
            return 'uniq' in authorization && !isNullish(authorization.uniq);
        }
    },

    async generateMessage(chain: Chain, erc4337?: ERC4337Options): Promise<string> {
        const domain = location.host;
        const userInfo = getUserInfo() as any;
        const address = isSolana(chain)
            ? window.particleAuth?.solana.selectedAddress
            : await getEVMPublicAddress({
                  ...erc4337,
                  chainId: chain.id,
              });
        const authType = getLatestAuthType() || 'email';
        const account = userInfo[authType] || userInfo[`${authType}_email`] || shortString(userInfo[`${authType}_id`]);
        const statement = `${authType.titleCase()}: ${account}`;
        const nonce = uuidv4().replace(/-/g, '');
        const issuedAt = new Date().toISOString();
        // 15分钟有效期
        const expirationTime = new Date(Date.now() + 10 * 60 * 1000).toISOString();
        return signInEthereumMessage.format(
            domain,
            address || '',
            statement,
            domain,
            chain.id.toString(),
            nonce,
            issuedAt,
            expirationTime
        );
    },

    async authorize(
        { authorization, chain }: LoginAuthorization,
        erc4337?: ERC4337Options
    ): Promise<AuthorizationResult> {
        const message = isSolana(chain)
            ? (authorization as SolanaAuthorization).message
            : toHexPrefixString(await this.generateMessage(chain, erc4337));
        const particleAuth = requireNotNullish(window.particleAuth);
        let signature;
        if (isSolana(chain)) {
            const result = await particleAuth.solana.signMessage(base58.decode(message));
            signature = Buffer.from(result).toString('base64');
        } else {
            signature = await particleAuth.ethereum.signMessage(message);
        }
        return {
            message,
            signature,
        };
    },
};

export default authorizeUtils;
