import React, { useEffect, useRef } from 'react';

const AuthCoreModalContainer = ({ setAuthCoreModal }: any) => {
    const rootModalContainerRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        setAuthCoreModal({
            rootModalContainer: rootModalContainerRef.current,
        });
    }, [setAuthCoreModal]);

    return <div ref={rootModalContainerRef}></div>;
};

export default AuthCoreModalContainer;
