import { Drawer } from 'antd';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useAuthCoreModal } from '../../context';
import { isMobile } from '../../utils';
import CircleClose from '../icon/circle-close';
import styles from './index.less';

interface IProps {
    children?: React.ReactNode;
    onClose: () => void;
    visible: boolean;
    title?: string;
    [key: string]: any;
}
const ParticleDrawer = (props: IProps) => {
    const { title, children, onClose, visible, ...rest } = props;
    const [mobile, setMobile] = useState<boolean>(isMobile());
    const { authCoreModal } = useAuthCoreModal();
    const dropDownRef = useRef({
        isDown: false,
        moveY: 0,
    });
    const drawerContentRef = useRef<HTMLDivElement>(null);

    const className = useMemo(() => {
        const classNames = [
            props.className,
            `particle-${mobile ? 'mobile' : 'pc'}-drawer`,
            'particle-drawer-container',
        ];
        return classNames.join(' ');
    }, [props.className, mobile]);

    const isScroll = (target: HTMLElement) => {
        let parentElement: HTMLElement | null = target;
        let flag = false;
        while (parentElement) {
            parentElement = parentElement.parentElement;
            if (parentElement?.classList.contains('ant-drawer-body')) {
                break;
            }
            if (parentElement?.scrollTop) {
                flag = true;
                break;
            }
        }
        return flag;
    };

    const drawerContainer = useMemo(() => {
        return props.containerSelector
            ? document.querySelector(props.containerSelector)
            : authCoreModal.rootModalContainer || document.querySelector('body');
    }, [props.containerSelector, authCoreModal.rootModalContainer]);

    useEffect(() => {
        if (!mobile || !drawerContentRef?.current) {
            return;
        }
        const touchstartHandler = (e: TouchEvent) => {
            if (e.type === 'touchstart' && isScroll(e.target as HTMLElement)) {
                return;
            }
            const clientY = e.touches[0].clientY;
            e.stopPropagation();
            dropDownRef.current.isDown = true;
            dropDownRef.current.moveY = clientY;
        };

        if (visible) {
            drawerContentRef?.current?.addEventListener('touchstart', touchstartHandler);
        }

        if (!visible) {
            drawerContentRef?.current?.removeEventListener('touchstart', touchstartHandler);
            dropDownRef.current.isDown = false;
            dropDownRef.current.moveY = 0;
        }

        return () => {
            drawerContentRef?.current?.removeEventListener('touchstart', touchstartHandler);
            dropDownRef.current.isDown = false;
            dropDownRef.current.moveY = 0;
        };
    }, [visible, mobile, drawerContentRef?.current]);

    useEffect(() => {
        if (!mobile || !drawerContentRef?.current) {
            return;
        }
        const touchendHandler = (e: TouchEvent) => {
            if (dropDownRef.current.isDown) {
                e.stopPropagation();
                dropDownRef.current.isDown = false;
            }
        };

        const touchmoveHandler = (e: TouchEvent) => {
            e.preventDefault();
            const clientY = e.touches[0].clientY;
            if (dropDownRef.current.isDown && clientY - dropDownRef.current.moveY > 60) {
                onClose();
            }
        };

        drawerContentRef?.current?.addEventListener('touchend', touchendHandler);

        drawerContentRef?.current?.addEventListener('touchmove', touchmoveHandler, { passive: true });

        return () => {
            drawerContentRef?.current?.removeEventListener('touchend', touchendHandler);
            drawerContentRef?.current?.removeEventListener('touchmove', touchmoveHandler);
        };
    }, [mobile, drawerContentRef?.current]);

    useEffect(() => {
        const resizeHandler = () => {
            setMobile(isMobile());
        };
        window.addEventListener('resize', resizeHandler);
        return () => {
            window.removeEventListener('resize', resizeHandler);
        };
    }, []);

    return (
        <Drawer
            onClose={onClose}
            open={visible}
            {...rest}
            className={className}
            destroyOnClose={false}
            getContainer={() => {
                return rest?.getContainer?.() || drawerContainer;
            }}
        >
            <div className="particle-drawer-content" ref={drawerContentRef}>
                <style>{styles as unknown as string}</style>
                <CircleClose
                    data-mobile={mobile}
                    className="close-icon"
                    onClick={() => {
                        onClose();
                    }}
                />
                <div className="particle-close-btn" data-mobile={mobile}></div>
                {title && <div className="particle-title">{title}</div>}
                {children}
            </div>
        </Drawer>
    );
};

export default ParticleDrawer;
